.profile__section {
    padding: 15rem 0;
    text-align: center;

    @include respond(tabPort) {
        padding: 10rem 0;
    }

    h2 {
        display: inline-block;

    }
}

.profile {
    font-size: 2rem;
    background: white;
    height: 40rem;

    @include respond(tabPort) {
        height: 60rem;
    }

    @include respond(phone) {
        height: 70rem;
    }

    &__image {
        background-size: cover;
        width: 100%;
        box-shadow: 0rem 0rem 2rem rgba($color: #000000, $alpha: 0.4);
        border-radius: 2rem;
        object-fit: cover;
        height: 100%;
        transition: all .3s;

        @include respond(xs) {
            width: 90%;

        }
    }

    &--name {
        font-size: 2.1rem;
        text-transform: uppercase;

        @include respond(phone) {
            font-size: 2.5rem;
        }
    }

    &:hover &__image {
        transform: translateY(-.4rem) scale(1.05);
        box-shadow: 0rem 0rem 4rem rgba($color: #000000, $alpha: 0.5);
        cursor: pointer;
    }

    &:active &__image {
        transform: translateY(0rem) scale(1);
        box-shadow: 0rem 0rem 3rem rgba($color: #000000, $alpha: 0.4);
    }
}